<template>
  <div style="font-family:Titillium Web;line-height: 1.0;background:black;" class="  p-3 ml-1 mr-1  shadow-lg rounded-lg animate__animated animate__zoomIn"
      :style="`${$store.state.data.layout.background_card2}`"
  >
    <div >
      <div class="row" >
        <div class="col-12">
          <div class="row" v-for="(item, index) in sertifikat" :key="index+'sertifikat'">
            <div class="col-3 text-center">
              <img :src="item.gambar" class="kinoLightBox img-fluid mx-auto" style="height:80px;width:80px;">
            </div>
            <div class="col-9">
              <p class="font-bold p-3">{{item.judul}}</p>
              <p class="pl-2 ">Credential ID  : <b>{{item.credential}}</b> Date : {{item.tanggal_expire}}</p>
              <p class="pl-2 mt-2">Credential URL : <a :href="item.link">{{item.link}}</a> </p>
              <hr class="style4 mt-4 mb-4">
            </div>
          </div>
        </div>
        <!--  -->
        <div class="col-12">
          <p class="font-semibold text-2xl p-3">SEMINAR</p>
          <hr class="style16">
        </div>
         <div class="col-12">
          <div class="row" v-for="(item, index) in seminar" :key="index+'sertifikat'">
            <div class="col-12">
              <p class="font-bold p-3">{{item.judul}}</p>
              <p class="pl-5 " v-html="item.keterangan"></p>
              <hr class="style4 mt-4 mb-4">
            </div>
          </div>
        </div>
        <!--  -->
         <div class="col-12">
          <p class="font-semibold text-2xl p-3">AWARD</p>
          <hr class="style16">
        </div>
         <div class="col-12">
          <div class="row" v-for="(item, index) in award" :key="index+'sertifikat'">
            <div class="col-12">
              <p class="font-bold p-3">{{item.judul}}</p>
              <p class="pl-5 " v-html="item.keterangan"></p>
              <hr class="style4 mt-4 mb-4">
            </div>
          </div>
        </div>
        <!--  -->
      </div>
    </div>
    <h1 style="font-size:1px;opacity:0.1;">Sertifikat TA_Maliki / Taufik Akbar Maliki PORTOFOLIO WEBSITE OFFICIAL</h1>
  </div>
</template>
<script>
import axios from 'axios'
// let rdb = firebase.database()
import autophp from '@/plugins/autophp'
let sdb = new autophp()
let urlnya='https://gis.officialapp.website'
export default {
  data () {
    return {
      sertifikat:[],
      seminar:[],
      award:[]
    }
  },
  layout: 'a-1',
  methods: {

  },
  mounted () {
    sdb.collection("app_portofolio_sertifikat",false,urlnya).doc().select(`select * from app_portofolio_sertifikat where iduser='${this.$store.state.id}'`).then(res => {
        this.sertifikat=res;
        this.$forceUpdate();
    });
    sdb.collection("app_portofolio_seminar",false,urlnya).doc().select(`select * from app_portofolio_seminar where iduser='${this.$store.state.id}'`).then(res => {
        this.seminar=res;
        this.$forceUpdate();
    });
    sdb.collection("app_portofolio_award",false,urlnya).doc().select(`select * from app_portofolio_award where iduser='${this.$store.state.id}'`).then(res => {
        this.award=res;
        this.$forceUpdate();
    });
    
  },
}
</script>
